.App {
  font-family: sans-serif;
  display: flex;
  flex-direction: column;
  padding: 2rem;
  min-height: 100vh;
  background: linear-gradient(#dddeda, #8a8989);
}

.App input {
  width: 4rem;
  font-size: 1.25rem;
  padding: 0.1rem;
  margin: 0rem auto 0.5rem auto;
  text-align: right;
  border-radius: 0.5rem;
}

.App button {
  margin: 0.5rem;
  padding: 0.5rem;
  border-radius: 0.5rem;
  background: rgb(160, 39, 39);
  color: white;
  border: none;
}

.App button:active {
  transform: translate(0.2rem, 0.2rem);
  background: rgb(132, 27, 27);
}

.App h1,
h2,
h3,
h4 {
  margin: 0rem 0rem 0.5rem 0rem;
}

.header {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-evenly;
  align-items: center;
  font-size: 2.5vw;
  text-align: center;
}

.header h1 {
  font-weight: 800;
}

.header img {
  height: 15vw;
  max-height: 5rem;
  width: auto;
  margin: 0rem 0rem 0.5rem 0rem;
}
